@import "src/styles/mixins";
@import "src/styles/themes";

.farms-editor-form .description-panel-content {
  display: flex;
  flex-flow: column nowrap;
  padding: 0 !important;
  @include themify($themes) {
    color: themed("colorContentTextDim");
  }
  background-color: transparent !important;
  box-shadow: 0px 0px 0px white;
  overflow: unset;
  font-weight: 100;
}
.geo-JSON-panel-header {
  padding-left: 5px;
  font-size: 13px;
  margin-top: -5px;
}
.description-panel-header-container {
  background-color: transparent !important;
  .panel-header-label {
    padding-left: 2px !important;
  }
  .description-panel-header {
    font-weight: bold;
    font-size: 16px;
    margin-bottom: 25px;
  }
  .farm-error-message {
    font-weight: normal;
  }
}

.markOnMapDisabled {
  font-size: 13px;
  margin-left: 42px;
  margin-bottom: 20px;
}
