@import "src/styles/themes";
@import "src/styles/mixins";

.pcs-renderer-cell.status {
  @include themify($themes) {
    &.online .pcs-renderer-icon svg {
      fill: themed("colorSystem");
    }
    &.offline .pcs-renderer-icon svg {
      fill: themed("colorWarning");
    }
    &.waiting .pcs-renderer-icon svg {
      size: 5px;
    }
  }
  &.waiting .pcs-renderer-icon svg {
    size: 5px !important;
    @include square-px-rem(15px);
  }
}

.farm-select-renderer {
  width: 100%;
  height: 100%;
  overflow: unset;
  top: -10% !important;
  display: inline-flex;
  .Select-control {
    min-width: 0px;
    background: transparent !important;
    height: 47px !important;
    border: transparent !important;
  }

  .Select-placeholder {
    display: flex;
    align-items: center;
    margin-top: 0px;
    padding-left: 0px;
    width: 120px;
    text-overflow: ellipsis;
  }
  .Select-multi-value-wrapper {
    height: 47px;
  }
  .Select-input {
    height: 100%;
    input {
      height: 100%;
    }
  }

  .Select-arrow-zone {
    pointer-events: none;
    height: 47px;
  }
  .Select-menu-outer {
    border: 1px solid #afb9c3;
    font-size: 70%;
    position: fixed;
    margin-top: 1px !important;
    border-top: none !important;
    .Select-menu {
      width: 140px;
      position: fixed;
      max-height: 100px;
      top: 73%;
      border: 1px solid #afb9c3;
      box-shadow: 1px 1px #777e85;
      min-height: 0px;
      @include themify($themes) {
        background: themed("colorSelectBackground");
      }
    }
    .Select-option {
      line-height: 1;
      overflow: visible !important;
      height: 25px;
      min-height: 0px;
    }
  }
}
.pcs-grid-container.ag-theme-dark .ag-row-focus {
  z-index: 1 !important;
}
