@import "src/styles/themes";
@import "src/styles/mixins";

.success-message {
  display: flex;
  flex-flow: row nowrap;
  flex-shrink: 0;
  font-size: 1em;

  .success-icon {
    @include square-px-rem(20px);
    @include rem-fallback(margin-right, 15px);

    svg {
      @include square-px-rem(14px);
    }
  }

  @include themify($themes) {
    color: themed("colorContentText");

    .success-icon svg {
      fill: themed("colorContentText");
    }
  }
}
