@import "src/styles/mixins";
@import "src/styles/themes";

.farms-editor-form {
  @include rem-font-size(15px);
  height: 800px;
  min-width: 500px;
  .farms-editor-header {
    @include rem-fallback(padding, 12px, 0px);
    @include rem-font-size(18px);
    font-weight: bold;
  }

  .form-placeholder {
    font-weight: bold;
    height: 30rem;
  }

  .svg-mandatory {
    margin-left: 5px;
    margin-top: -10px;
    float: left;
  }
  .svg-mandatory svg {
    @include themify($themes) {
      fill: themed("colorSvg");
    }
    height: 0.627em;
    width: 0.627em;
  }
  .farms-editor-error {
    border: 1px transparent solid;
    @include rem-fallback(margin, 12px, 0px);
    @include rem-fallback(padding, 16px);
  }

  .farms-editor-success {
    border: 1px transparent solid;
    @include rem-fallback(margin, 12px, 0px);
    @include rem-fallback(padding, 16px);
  }

  .form-control-inputs {
    line-height: 1.2;
    height: 120px;
    font-weight: bold;

    .form-control-group {
      margin-top: 0px;
      width: 33.33%;
      display: inline-flex;
      @include rem-fallback(padding, 0px, 6px);
      align-items: center;
      // font-weight: 100;
      font-size: 15px;
      .input-label {
        margin-bottom: 15px;
      }
      .form-control-container {
        margin-top: 45px;
        min-width: 80px;
        width: 75%;
        height: 85px;
        font-size: 14px;
        .error-message {
          font-weight: 100;
        }

        .farms-editor-input {
          margin-left: 5px;
          width: 85%;
          border: 1px solid #afb9c3;
          border-radius: 3px;
          text-overflow: ellipsis;
          padding-right: 2px;

          .Select-control {
            font-weight: normal;
          }

          &.select-container {
            font-weight: normal;
          }

          &.error {
            margin-top: 0px;
            font-weight: 100;
          }
          .Select-menu-outer {
            margin-top: 1px !important;
            border-top: none !important;
            border: 1px solid #afb9c3;
          }

          .Select-placeholder {
            width: 70%;
            display: flex;
            align-items: center;
            font-weight: 100;
            text-overflow: ellipsis !important;
          }

          .Select-control {
            border: none !important;

            .Select-input {
              height: 100%;
              input {
                height: 100%;
              }
            }
          }
        }
      }
    }
  }

  .wait-indicator {
    @include rem-fallback(margin-left, 8px);
  }

  .summary-icon svg {
    @include square-px-rem(12px);
    @include rem-fallback(margin-left, 4px);
  }

  .button-box {
    padding-top: 10px;
    .close-editor {
      min-width: 0px;
      width: auto;
      border-radius: 3px;
    }
  }

  .tooltip {
    position: relative;
    display: inline-block;
    margin-bottom: 8px;
  }

  .tooltip .tooltiptext {
    visibility: hidden;
    width: 120px;
    border-radius: 6px;
    padding: 5px 0px 5px 5px;
    font-size: 12px;
    font-weight: 100;
    /* Position the tooltip */
    position: absolute;
    z-index: 1;
    top: 107%;
    left: 65%;
    margin-left: -35px;
  }

  .tooltip .tooltiptext::after {
    content: "";
    position: absolute;
    bottom: 100%;
    left: 28.5%;
    margin-left: -5px;
    border-width: 5px;
    border-style: solid;
  }
  .tooltip:hover .tooltiptext {
    visibility: visible;
  }

  @include themify($themes) {
    color: themed("colorContentTextDim");

    .farms-editor-header {
      color: themed("colorContentText");
    }

    .farms-editor-error {
      border-color: themed("colorAlert");
    }

    .farms-editor-success {
      border-color: themed("colorContentText");
    }

    .summary-icon svg {
      fill: themed("colorContentText");
    }

    .tooltip .tooltiptext {
      background-color: themed("colorTooltipBackground");
      color: themed("colorTooltipText");
      border: 1px solid themed("colorTooltipBorder");
      box-shadow: 3px 3px 6px themed("colorTooltipDropShadow");
    }
    .tooltip .tooltiptext::after {
      border-color: transparent transparent themed("colorTooltipBorder");
    }
    #applyBtnDisabled {
      background-color: themed("colorBtnBackgroundDisabled");
      color: themed("colorBtnTextDisabled");
    }
  }
}

@media (max-width: 1200px) {
  .farms-editor-form {
    height: 940px;

    .form-placeholder {
      height: 750px;

      .grid-container .grid-cell {
        &.col-2 {
          height: 170px;
          min-height: 150px;
        }
      }
      .grid-container .grid-cell {
        &.col-8 {
          height: 550px;
          min-height: 150px;
        }
      }
    }
  }
}

#applyBtn {
  width: 10%;
  background-color: rgb(15, 125, 252);
  color: white;
  border-radius: 3px;
}

#applyBtnDisabled {
  width: 10%;
  border-radius: 3px;
}

#geojson-source-url {
  font-size: 12px;
  font-weight: bold;
}
