@import "src/styles/mixins";
@import "src/styles/themes";

.grid-farm-maps{
  flex-basis: 100%;
  margin-left: 1%;
  font-weight: solid;
   margin-top: 27px;
  margin-left: 0.5em;
}

.theme-light .select-container.Select .Select-control {
  border: 0px solid #afb9c3;
  border-radius: 4px;
}
.farmdetail-menus .btn{
  width:auto;
  padding:0px 19px;
  padding-right:0px;
  min-width:auto;
}

.precision-maps-group-dropdown {
  @include rem-fallback(width, 185px);
  margin-top: 9.5px;
  .Select-menu-outer {
    margin-top: 1px !important;
    border-top: none !important;
  }

  .Select-control {
    // border-top: 1px solid black !important;
    // border-left: 1px solid black !important;
    // border-bottom: 1px solid black !important;
    // border-right: 1px solid black !important;
    height: 100% !important;
    width: auto;
    border: none !important;
    margin-left:10px;
    .Select-placeholder {
      display: flex;
      @include themify($themes) {
        color: themed("mapModalHeaderColor");
      }
      font-size: 14px !important;
      align-self: center;
      padding-top: 1.4%;
      margin-left: 2%;
      padding-left: 0%;
    }

    .Select-input {
      height: 100%;
      width: 100%;

      input {
        height: 100%;
      }
    }
  .Select-arrow{
    border-color: #0078D4 transparent transparent !important;
  }
    .Select-arrow-zone {
      cursor: default;
      height: 25px;
      margin-left: 5px;
    }
  }

  @include themify($themes) {
    .Select-control,
    .Select-menu-outer {
      background-color: themed("colorContextMenuBg") !important;
      border-color: themed("colorHeaderBorderColor") !important;
    }
  }
}

.farm-details-menu-buttons {
  // flex-basis: 8%;
  // display: flex;
  width: auto;
  padding: 0px;
}

.farm-details-menu-buttons svg{
fill:#0078D4 !important;
}
.farmDetails-container {
  height: 100%;
  display: flex;
  flex-grow: 1;
  flex-flow: column nowrap;
  align-items: stretch;

  .grid-container {
    padding: 10px;

    .grid-cell {
      min-height: 400px;

      &.devices-overview-cell {
        min-width: 314px;
      }
    }
  }

  @include themify($themes) {
    background-color: themed("colorContentBackground");
    color: themed("colorContentText");
  }
}
.refresh-btn svg{
  fill:#0078D4 !important;
}
.grid-farm-container {
  display: flex;
  flex-flow: row wrap;

  .grid-farm-cell {
    display: flex;
    flex-basis: 50%;
    flex-grow: 1;

    &.h {
      height: 10%;
    }
    &.col-1 {
      flex-basis: 10%;
    }
    &.col-15 {
      flex-basis: 20%;
    }
    &.col-2 {
      flex-basis: 20%;
    }
    &.col-3 {
      flex-basis: 30%;
    }
    &.col-35 {
      flex-basis: 35%;
    }
    &.col-4 {
      flex-basis: 40%;
    }
    &.col-45 {
      flex-basis: 40%;
    }
    &.col-5 {
      flex-basis: 50%;
    }
    &.col-6 {
      flex-basis: 60%;
    }
    &.col-7 {
      flex-basis: 70%;
    }
    &.col-8 {
      flex-basis: 80%;
    }
    &.col-9 {
      flex-basis: 90%;
    }

    .grid-farm-cell-contents {
      display: flex;
      margin: 5px;
      flex-grow: 1;
      position: relative;
    }
  }
}

@media (max-width: 1180px) {
  .grid-farm-container .grid-farm-cell {
    &.col-1,
    &.col-2,
    &.col-3 {
      flex-basis: 55%;
    }
    &.col-15 {
      flex-basis: 35%;
    }
    &.col-45 {
      flex-basis: 90%;
    }
    &.col-4,
    &.col-5,
    &.col-6,
    &.col-7,
    &.col-8,
    &.col-9 {
      flex-basis: 100%;
    }
  }
}

@media (max-width: 700px) {
  .grid-farm-container .grid-farm-cell {
    &.col-1,
    &.col-2,
    &.col-3 {
      flex-basis: 90%;
    }
    &.col-15 {
      flex-basis: 90%;
    }
    &.col-45 {
      flex-basis: 90%;
    }
    &.col-4,
    &.col-5,
    &.col-6,
    &.col-7,
    &.col-8,
    &.col-9 {
      flex-basis: 100%;
    }
  }
}

@media (max-width: 1200px) {
  .farmDetails-container {
    height: auto;
  }

  .farm-details-menu-buttons {
    // flex-basis: 8%;
    // display: flex;
    width: 10%;
    padding: 0px;
  }
}

@media (max-width: 800px) {
  .farmDetails-container {
    height: auto;
  }

  .farm-details-menu-buttons {
    // flex-basis: 8%;
    // display: flex;
    width: 6%;
    padding: 0px;
  }
}

.farm-details-link{
  margin-left: 1em;
 margin-top: 4px;
 margin-bottom: 20px;

}

.farm-details-link a{
  color: #0078D4;
  text-decoration:none;
}
