@import "src/styles/variables";
@import "src/styles/mixins";
@import "src/styles/themes";

$btnHeight: 24px;
$btnMinWidth: 120px;
$btnIconSize: 14px;
.add-device-container .ag-layout-normal{
  overflow-x:hidden !important;
}

.add-device-container .flyout-content .summary-section{
border-top-color: transparent !important;
 margin-top:5px;
  padding-top:0px;
}

.add-devices-error {
  border: 1px transparent solid;
  @include rem-fallback(margin, 12px, 0px);
  @include rem-fallback(padding, 16px);
}

.add-devices-success {
  border: 1px transparent solid;
  @include rem-fallback(margin, 12px, 0px);
  @include rem-fallback(padding, 16px);
}

.add-device-content {
  display: flex;
  flex-flow: column nowrap;
  padding: $baseContentPadding;

  .tab-container {
    display: flex;
    flex-flow: row wrap;
    flex-shrink: 0;

    .tab {
      text-decoration: none;
      padding: 5px 0px;
      padding-bottom: 10px;
      font-weight: bold;
      margin-right: 10px;
      background: none;
      border: none;
      cursor: pointer;
      @include rem-font-size(15px);
      .btn-text {
        @include rem-fallback(padding-left, (($btnHeight - $btnIconSize) / 2));
      }
      width: auto;
      min-width: 0px;
    }
  }

  .grid-container {
    display: flex;
    flex-flow: column nowrap;
    flex-grow: 1;

    .temp-row {
      text-decoration: none;
      margin: 10px 0px;
      padding: 10px;
    }

    .padded-error {
      @include rem-fallback(padding-top, 20px);
    }
  }

  .button-box {
    padding-top: 0px;

    .selected-device-summary {
      margin: 6px 200px 0px 0px;
      font-weight: bold;
    }
    #applyBtn {
      width: 100px;
      height: 30px;
      background-color: rgb(15, 125, 252);
      color: white;
      border-radius: 3px;
      padding: 0;
      margin-right: 4px;
    }

    #cancelBtn {
      width: 100px;
      height: 30px;
      border-radius: 3px;
      padding: 0px 0px 0px 6px;
      min-width: 0px;
    }

    #applyBtnDisabled {
      width: 100px;
      height: 30px;
      background-color: #cae3ff;
      color: white;
      border-radius: 3px;
      padding: 0;
      margin-right: 4px;
    }
  }

  @include themify($themes) {
    color: themed("colorContentTextDim");

    .tab-container {
      border-bottom: 1px solid #cacaca;

      .tab {
        color: themed("colorContentTextDim");

        &:hover {
          color: themed("colorContentText");
        }

        &.active {
          color: themed("colorContentText");
          border-bottom: 4px solid themed("colorTabBorder");
          padding-bottom: 6px;
        }
      }
    }

    .add-devices-success {
      border-color: themed("colorContentText");
    }

    .temp-row {
      background-color: themed("colorGridRowBackground");
      color: themed("colorGridCellText");

      &:hover {
        background-color: themed("colorGridRowHoverBackground");
      }
    }
  }
}
