@import 'src/styles/themes';
@import 'src/styles/mixins';

.rulesview-flyout{
  .flyout-container{
    width:900px;
  }
}
.ruledetails-table{
  padding-top:0px !important;
}
.view-rule-flyout-container {
  .ruleflyout-section-header{
    font-weight:600;
    font-size:16px !important;
  }

  .rule-description{
    padding-top:15px;
    table-layout: auto;
    th{
      display:table-cell !important;
    }
  }

  .rule-description th{
    width:24.8%;
    padding-left:0.30%;
  }
  .ruleviewer-table table{
    width: 100%;
    table-layout: fixed;

  }
  .ruleviewer-table table tr th{
    display:table;
  }
  .ruleviewer-table table tr th,td{
    text-align:inherit;
  }
  .ruleviewer-table table tr td{
    text-indent: -7px;
    padding: 3px;
    padding-bottom: 8px;
    word-wrap: break-word;
    display: table-cell;
    vertical-align: baseline;
    vertical-align: initial;

  }
.property-grid-container{
  padding-left:0px;
  .row{
    padding-left:0px;
  }
}
.row-grid {
    .row{
     padding:0px;
    }
    .cell{
      justify-content: left;
      padding:5px;
      height:32px;
    }
}
  .rule-name {
    @include rem-fallback(font-size, 16px);
    @include rem-fallback(margin-top, 32px);
    font-weight:600;

}

.rule-action div:first-child{
   margin-top:5px;
}
  .rule-prop-label {
    @include rem-fallback(margin-top, 15px);
     font-weight:600;
  }

  .rule-prop-value {
    @include rem-fallback(margin-top, 5px);

  }

  .summary-icon svg {
    @include square-px-rem(16px);
    @include rem-fallback(margin-left, 8px);
  }

  @include themify($themes) {
    color: themed('colorContentText');

    .summary-icon svg { fill: themed('colorContentText'); }
  }

  .farmLink {
    text-decoration: none;
    font-weight: bold;
    color: #0078D4;
  }
}
