.map-image {
  width: 55%;
  height: auto;
  float: center;
  margin-left: 25%;
  max-width: 75%;
  position: relative;
  min-height: 200px;
  padding-top: 50px;
  margin-top: 1%;
  margin-bottom: 5%;
}

.shimmer-modal {
  display: block !important;
  width: 45%;
  height: auto;
  float: center;
  margin-left: 25%;
  max-width: 75%;
  position: relative;
  min-height: 200px;
  padding-top: 50px;
  margin-top: 5%;
  margin-bottom: 5%;
  background: #f6f7f8;
  background-image: linear-gradient(
    to right,
    #f6f7f8 0%,
    #edeef1 20%,
    #f6f7f8 40%,
    #f6f7f8 100%
  );
  background-repeat: no-repeat;
  display: inline-block;
  position: relative;

  -webkit-animation-duration: 1s;
  -webkit-animation-fill-mode: forwards;
  -webkit-animation-iteration-count: infinite;
  -webkit-animation-name: placeholderShimmer;
  -webkit-animation-timing-function: linear;
}

.modal-2-container {
  overflow-y: scroll;
  width: 100%;
  height: 100%;
  position: fixed;
  display: flex;
  align-items: center;
  justify-content: center;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 1000;
}

.modal-map {
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 0vh;
  background-color: transparent;
  overflow: hidden;
  transition: background-color $base-duration ease;
  z-index: 9999;

  &.open-map {
    position: fixed;
    width: 100%;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.5);
    transition: background-color $base-duration;
    @include themify($themes) {
      .content-wrapper-map {
        min-height : 550px;
        transform: scale(1);
        background-color: themed("mapModalBackground");
        border: 1px solid themed("mapModalBorder");
        > .properties {
          color: themed("mapModalcolor");
        }
      }
    }
  }
}

.content-wrapper-map {
  position: relative;
  //display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  width: 60%;
  height: auto;
  max-height: 100%;
  min-width: 500px;
  margin: 0;
  padding: 1.5rem 0 1.5rem 0;
  background-color: white;
  box-shadow: 0 0 2.5rem rgba(0, 0, 0, 0.5);
  transform: scale(0);
  transition: transform $base-duration;
  transition-delay: 0.15s;
}

#disagree {
  text-align: center;
  color: #eb0716;
  padding-top: 1rem;
  font-size: 0.8rem;
}

