@import "src/styles/variables";
@import "src/styles/mixins";
@import "src/styles/themes";

.devices-container {
  padding: $baseContentPadding;

  @include themify($themes) {
    .status {
      color: themed("colorContentText");
    }
  }
}
.devices-container .flyout-container {
  width: 900px;
}

.theme-light .property-grid-container .row .cell {
  flex-grow: inherit;
  color: #333;
}
.property-grid-container .row .cell {
  flex-direction: inherit;
}

.device-details-container {
  .device-property-container {
    border: none;
    @include rem-fallback(margin-top, 20px);
  }

  .device-title {
    @include properties-title;
  }
}
