@import 'src/styles/themes';
@import 'src/styles/mixins';

.pcs-renderer-cell.status {
  @include themify($themes) {
    &.online .pcs-renderer-icon svg { fill: themed('colorSystem'); }
    &.offline .pcs-renderer-icon svg { fill: themed('colorWarning'); }
    &.waiting .pcs-renderer-icon svg { size: 5px }

  }

  &.waiting .pcs-renderer-icon svg{
    size: 5px !important;
    @include square-px-rem(15px);
  }
  .fail-info svg{
    width:.728em;
    height:.728em;
    margin:5px;
    @include themify($themes) {
    fill: themed("colorMapsSvg");
  }

  }
 .tooltip {
    position: relative;
    display: inline-block;
    line-height:20px;
    z-index:10 !important;
  }

  .tooltip .tooltiptext {
    visibility: hidden;
    color:#333;
    min-width:500px;
   max-width:1000px;
   height:80px;
    border-radius: 6px;
    padding: 5px 0px 5px 5px;
    font-size: 12px;
    font-weight: 100;
    /* Position the tooltip */
    position: absolute;
        border: 1px solid #bfbfbf;
        background:#fff;
    box-shadow: 3px 3px 6px rgba(0, 0, 0, 0.1);
    top: 108%;
    margin-left: -315px;
    white-space:initial;
     z-index:10 !important;
     text-align: justify;
     word-wrap: break-word;
user-select:text;
-webkit-user-select: text;
-ms-user-select:text;
  }
  .tooltip .tooltiptext::after {
    content: "";
     position: absolute;
  z-index: 9999 !important;
  right: calc(50% - 10px);
  top: -10px;
  border-style: solid;
  border-width: 0 10px 10px 10px;
  border-color: transparent transparent #bfbfbf transparent;
  transition-duration: 0.3s;
  transition-property: transform;
  }
  .tooltip:hover .tooltiptext {
    visibility: visible;

   }
.error-text{
  width:600px !important;
  height:70px !important;
  overflow-y:auto !important;
display:block;
}


}
