@import "src/styles/variables";
@import "src/styles/mixins";
@import "src/styles/themes";

$btnHeight: 24px;
$btnMinWidth: 120px;
$btnIconSize: 14px;

.devices-container .associate-device-container .flyout-container {
  width: 530px;
  min-width: 500px;
}

.associate-devices-error {
  border: 1px transparent solid;
  @include rem-fallback(margin, 12px, 0px);
  @include rem-fallback(padding, 16px);
}

.associate-devices-success {
  border: 1px transparent solid;
  @include rem-fallback(margin, 12px, 0px);
  @include rem-fallback(padding, 16px);
}

.associate-device-content {
  display: flex;
  flex-flow: column nowrap;
  padding: $baseContentPadding;

  .farm-applytoall-group {
    margin-top: 25px;
    margin-bottom: 10px;
    display: flex;
    border: 1px solid #afb9c3;
    border-radius: 3px;

    .farm-apply-to-all-btn {
      display: flex;
      border-left: 1px solid #afb9c3;
      min-width: 0px;
      width: 30%;
    }
    .form-control-container {
      width: 70%;
      .farm-applytoall-select {
        border-right: 1px solid #afb9c3;

        text-overflow: ellipsis;
        display: flex;
        .Select-menu-outer {
          margin-top: 1px !important;
          border-top: none !important;
          border: 1px solid #afb9c3;
          .Select-menu {
            position: fixed;
            max-height: 100px;
            border: 1px solid #afb9c3;
            box-shadow: 1px 1px #777e85;
            min-height: 0px;
            @include themify($themes) {
              background: themed("colorSelectBackground");
            }
          }
        }

        .Select-control {
          border: none !important;

          .Select-placeholder {
            display: flex;
            align-items: center;
          }

          .Select-input {
            height: 100%;

            input {
              height: 100%;
            }
          }
        }
      }
    }
  }

  .tab-container {
    display: flex;
    flex-flow: row wrap;
    flex-shrink: 0;

    .tab {
      width: auto;
      text-decoration: none;
      padding: 5px 0px;
      padding-bottom: 10px;
      font-weight: bold;
      margin-right: 10px;
      background: none;
      border: none;
      cursor: pointer;
      @include rem-font-size(15px);
      .btn-text {
        @include rem-fallback(padding-left, (($btnHeight - $btnIconSize) / 2));
      }
      width: auto;
      min-width: 0px;
    }
  }

  .grid-container {
    display: flex;
    flex-flow: column nowrap;
    flex-grow: 1;

    .temp-row {
      text-decoration: none;
      margin: 10px 0px;
      padding: 10px;
    }

    .padded-error {
      @include rem-fallback(padding-top, 20px);
    }
  }

  .button-box {
    padding-top: 0px;

    .selected-device-summary {
      margin: 6px 200px 0px 0px;
      font-weight: bold;
    }

    #cancelBtn {
      width: 100px;
      height: 30px;
      border-radius: 3px;
      padding: 0px 0px 0px 6px;
      min-width: 0px;
    }

    #applyBtn {
      width: 100px;
      height: 30px;
      background-color: rgb(15, 125, 252);
      color: white;
      border-radius: 3px;
      padding: 0px 0px 0px 6px;
      min-width: 0px;
      margin-right: 4px;
    }

    #applyBtnDisabled {
      width: 100px;
      height: 30px;
      background-color: #cae3ff;
      color: white;
      border-radius: 3px;
      min-width: 0px;
      padding: 0px 0px 0px 6px;
      margin-right: 4px;
    }
  }

  @include themify($themes) {
    color: themed("colorContentTextDim");

    .tab-container {
      border-bottom: 1px solid #cacaca;

      .tab {
        color: themed("colorContentTextDim");

        &:hover {
          color: themed("colorContentText");
        }

        &.active {
          color: themed("colorContentText");
          border-bottom: 4px solid themed("colorTabBorder");
          padding-bottom: 6px;
          width: auto;
        }
      }
    }

    .associate-devices-success {
      border-color: themed("colorContentText");
    }

    .temp-row {
      background-color: themed("colorGridRowBackground");
      color: themed("colorGridCellText");

      &:hover {
        background-color: themed("colorGridRowHoverBackground");
      }
    }
  }
}
