@import "src/styles/variables";
@import "src/styles/mixins";
@import "src/styles/themes";

.maps-container {
  padding: $baseContentPadding;
}
.maps-select-list {
  width: 250px;
  height: 30px;
}

.theme-light .select-container.Select .Select-control {
  border: 0px solid #afb9c3;
  border-radius: 3px;
}

.maps-group-dropdown {
  @include rem-fallback(width, 150px);
  margin-top: 4px;
  .Select-menu-outer {
    margin-top: 1px !important;
    border-top: none !important;
  }

  .Select-control {
    height: 100% !important;
    width: 100%;

    .Select-placeholder {
      display: flex;
      align-self: center;
      padding-top: 5%;
      padding-left: 20%;
      @include themify($themes) {
        color: themed("mapModalHeaderColor");
      }
      font-size: 14px !important;
    }
    .Select-arrow-zone {
      height: 22px;
    }

    .Select-input {
      height: 100%;
      width: 80%;

      input {
        height: 100%;
      }

      .maps-select {
        border: 0px solid #afb9c3;
        border-radius: 3px;
      }
    }
  }

  @include themify($themes) {
    .Select-control,
    .Select-menu-outer {
      background-color: themed("colorContextMenuBg") !important;
      border-color: themed("colorHeaderBorderColor") !important;
    }
  }
}

.maps-group-dropdown.Select-control :hover {
  background-color: transparent transparent #7c7c7c transparent;
}
