@import "src/styles/mixins";
@import "src/styles/themes";

$zoomBtnSize: 30px;
$zoomBtnFont: 20px;
.right {
  float: right;
}

.button-option {
  width: 100%;
  font-size: 70%;
  background: transparent;
  border: 0px;
  height: 4vh;
  min-height: 0px;
  padding: 0px;
  margin-left: 0px;
  padding-right: 30px;
}
.empty-container {
  flex-basis: 90%;
  flex-shrink: 3;
}

.cancel-btn svg {
  @include square-px-rem(18px);
  @include themify($themes) {
    fill: themed("mapModalHeaderColor");
  }
  right: 0px;
  // width: 20px;
  // height: 20px;
  // padding-left: 0px;
  // padding-right: 0px;
  // padding-top : 0%;
  margin-top: 0%;
  // min-width: 0;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  font-size: $zoomBtnFont;
  width: $zoomBtnSize;
  height: $zoomBtnSize;
  background: transparent;
  margin-left: 0%;
  margin-right: 2%;
  line-height: $zoomBtnSize;
  border: none;
  cursor: pointer;
}

.header-modal {
  margin-left: 1.6%;
  flex-basis: 20%;
  margin-top: 2%;
}
.map-modal-header {
  @include themify($themes) {
    color: themed("mapModalHeaderColor");
  }
  position: relative;
  display: inline-flex;
  flex-direction: row;
  justify-content: space-between;
  width: 100%;
  padding: 0 0 1rem;
  margin-left: 3px;
  padding-bottom: 7px;
  .modal-header-content {
    margin-right: 5%;
  }
  .btnclass {
    height: 30px;
    padding-left: 0px;
    padding-right: 0px;
    margin-top: 2%;
    margin-right: 2%;
    width: auto;
    font-size: 25px;
  }

  .btncancel {
    align: right;
    margin-left: 0px;
    margin-right: 5%;
    width: 20px;
    height: 20px;
    padding-left: 0px;
    padding-right: 0px;
    padding-top: 0%;
    margin-top: 1%;
    min-width: 0;

    .injected-svg {
      height: 10%;
      width: 10%;
    }
  }

  .svg-container btn-icon {
    height: 10%;
    width: 10%;
  }
}
