@import "src/styles/mixins";
@import "src/styles/themes";
@import "src/styles/variables";

.title {
  font-display: solids;
}

.heading {
  margin-top: 5%;
  margin-bottom: 2%;
  font-display: solid;
}

.select-container.Select .Select-value {
  padding-bottom:0.2rem;
}


.reset-btn {
  border-radius: 3px !important;
}
.apply-btn {
  background-color: rgb(15, 125, 252) !important;
  color: white !important;
  border-radius: 3px !important;
  width: 5% !important;
}

.wrapper-button-container{
  margin-top: 1em
}

.date-picker-container{
  position:relative;
}

.datepicker-icon{
  position: absolute;
    right: 2%;
    top: 16%;
}

.datepicker-textbox{
  background-color: inherit;
  border: 0.5px solid #afb9c3;
  border-radius: 3px;
  line-height: 1.5;
  font-family: inherit ;
  padding-left: 8px;
  width: 28.8em;
}

.datepicker-textbox::placeholder{
  color: #c4c4c4
}


.btn-primary {
  color: #fff;
  background-color: #337ab7;
  border-color: #2e6da4;
}
.daterangepicker .btn{
  display:initial !important;
}

.btn-primary.active,
.btn-primary.focus,
.btn-primary:active,
.btn-primary:focus,
.btn-primary:hover,
.open > .dropdown-toggle.btn-primary {
  color: #fff;
  background-color: #286090;
  border-color: #204d74;
}

.btn-primary.active,
.btn-primary:active,
.open > .dropdown-toggle.btn-primary {
  background-image: none;
}

.btn-primary.disabled,
.btn-primary.disabled.active,
.btn-primary.disabled.focus,
.btn-primary.disabled:active,
.btn-primary.disabled:focus,
.btn-primary.disabled:hover,
.btn-primary[disabled],
.btn-primary[disabled].active,
.btn-primary[disabled].focus,
.btn-primary[disabled]:active,
.btn-primary[disabled]:focus,
.btn-primary[disabled]:hover,
fieldset[disabled] .btn-primary,
fieldset[disabled] .btn-primary.active,
fieldset[disabled] .btn-primary.focus,
fieldset[disabled] .btn-primary:active,
fieldset[disabled] .btn-primary:focus,
fieldset[disabled] .btn-primary:hover {
  background-color: #337ab7;
  border-color: #2e6da4;
}
.btn-primary .badge {
  color: #337ab7;
  background-color: #fff;
}
