@import 'src/styles/variables';
@import 'src/styles/mixins';
@import 'src/styles/themes';

.jobs-container {
  padding: $baseContentPadding;
}

.jobs-select-list {
  width: 250px;
  height: 30px
}

.abort-job {
  display: flex;
  padding: 0px 12px 0px 12px;
}

jobs-group-dropdown {
  @include rem-fallback(width, 200px);
  margin-top: 4px;

  .Select-menu-outer {
    margin-top: 1px !important;
    border-top: none !important;
  }

  .Select-control {
    // border-top: 1px solid black !important;
    // border-left: 1px solid black !important;
    // border-bottom: 1px solid black !important;
    // border-right: 1px solid black !important;
    height: 100% !important;
    width: 100%;

    .Select-placeholder {
      display: flex;
      align-self: center;
      padding-top: 4%;
      padding-left: 20%
    }

    .Select-input {
      height: 100%;
      width: 80%;

      input {
        height: 100%;
      }
    }

  }

  @include themify($themes) {

    .Select-control,
    .Select-menu-outer {
      background-color: themed('colorContextMenuBg') !important;
      border-color: themed('colorHeaderBorderColor') !important;

    }

  }



}

.jobs-group-dropdown.Select-control :hover {
  background-color: transparent transparent #7c7c7c transparent;
}