@import "src/styles/themes";
@import "src/styles/mixins";

.rule-message {
  font-weight: bold;
  margin-top: 5%;
  margin-bottom: 2%;
}

.field-required {color:red;}
.add-condition{padding-left:0px;}
.new-rule-flyout-container {
  .rule-property-container {
    border: none;
    @include rem-fallback(margin-top, 20px);
  }

  .padded-top {
    margin-left: 0px;
    padding-left: 0px;
    @include rem-fallback(margin-top, 24px);
  }

  .rules-errors {
    font-weight: bold;
    margin-top: 5%;
    margin-bottom: 2%;
    color: red;
  }

  .rule-editor-success {
    border: 1px transparent solid;
    @include rem-fallback(margin, 12px, 0px);
    @include rem-fallback(padding, 16px);
  }
  .rule-editor-error {
    border: 1px transparent solid;
    @include rem-fallback(margin, 12px, 0px);
    @include rem-fallback(padding, 16px);
  }

  .summary-icon svg {
    @include square-px-rem(16px);
    @include rem-fallback(margin-left, 8px);
  }

  .email-toggle-container {
    display: flex;
  }

  .farm-dropdown .Select-control {
    border: 1px solid #3e4145 !important;
  }
  .farm-dropdown .Select-control:hover {
    border: 1px solid #afb9c3 !important;
  }
  .farm-dropdown .select-container.Select.error .Select-control {
    border-color: #fc540a !important;
  }
  @include themify($themes) {
    .rule-editor-error {
      border-color: themed("colorAlert");
    }

    .rule-editor-success {
      border-color: themed('colorContentText');
    }
    .summary-icon svg {
      fill: themed("colorContentText");
    }

    input.form-control,
    textarea.form-control {
      color: themed("colorContentText");
    }
  }
}

#applyRuleBtn {
  width: 30%;
  background-color: rgb(15, 125, 252);
  color: white;
  border-radius: 3px;
}

#applyRuleBtnDisabled {
  width: 30%;
  background-color: rgb(202, 227, 255) !important;
  color: white;
  border-radius: 3px;
}
