.content {
  font-family: Segoe UI;
  line-height: normal;
  position: relative;
  display: flex;
  height: 400px;
  padding: 1rem 3rem 1rem 3rem;
  text-align: justify;
  box-shadow: 0px .5px 0px #D8D8D8, 0px -.5px 0px #D8D8D8;
  overflow-y: auto;

  div {
    font-size: 0.875rem;
    line-height: 1.5;
  }

  ol{
    margin: 0px;
    padding: 1rem;
  }
}
