@import "src/styles/themes";
@import "src/styles/mixins";

.pcs-renderer-cell.status {
  @include themify($themes) {
    &.online .pcs-renderer-icon svg {
      fill: themed("colorSystem");
    }
    &.offline .pcs-renderer-icon svg {
      fill: themed("colorWarning");
    }
    &.waiting .pcs-renderer-icon svg {
      size: 5px;
    }
  }
  &.waiting .pcs-renderer-icon svg {
    size: 5px !important;
    @include square-px-rem(15px);
  }
}

.download-icon svg {
  @include square-px-rem(15px);
  margin-top: 5%;
  margin-left: 5px;
  margin-right: 7px;
}

.download {
  font-size: 70%;
}
.download-inline {
  display: inline-flex !important;
  width: 120px !important;
  //padding-top: 2px !important;
  overflow: unset;
  background: solid;

  .download-fileName {
    width: 90% !important;
    font-size: 13px !important;
    margin-left: 5px !important;
    padding-left: 4px !important;
    padding-bottom: 7px;
    overflow: unset;
  }
  .download-right-icon svg {
    @include square-px-rem(20px);
    align-self: right;
    padding-right: 5px;
    padding-bottom: 7px;
    overflow: unset;
  }
}
.download-renderer-dropdown {
  @include rem-fallback(width, 120px);
  height: 20px;
  overflow: unset;
  padding-right: 0px;
  align-items: center;
  right: 50px;
  display: inline-flex;
  align-self: flex-end;
  margin-left: 25%;
  padding-bottom: 30%;
  .Select-control {
    border: none !important;
    min-width: 0px;
    background: transparent !important;
  }

  .Select-placeholder {
    display: flex;
    align-items: center;
    font-size: 95%;
    margin-top: 0px;
    padding-left: 0px;
    width: 120px;
    text-overflow: ellipsis;
    @include themify($themes) {
      color: themed("mapModalHeaderColor") !important;
    }
  }

  .Select-input {
    input {
      height: 100%;
    }
    margin-left: 0px;
  }

  .Select-arrow-zone {
    cursor: default;
    pointer-events: none;
    height: 45px;
    size: 90%;
  }
  .Select-menu-outer {
    border: 1px solid #afb9c3;
    font-size: 70%;
    //flex-basis: 80%;
    position: fixed;
    z-index: 1000 !important;
    overflow: visible !important;

    .Select-menu {
      position: fixed;
      overflow: visible !important;
      top: 93%;
      border: 1px solid black;
      box-shadow: 1px 1px;
      z-index: 99999 !important;
      min-height: 0px;
      background: #fff;
      @include rem-fallback(max-height, 200px);
    }

    .Select-option {
      overflow: visible !important;
      background: white;
      height: 42px;
      min-height: 0px;
      padding-right: 0px;
      padding-top: 0px !important;
      margin-top: 0px !important;
      padding-left: 3px;
      align-items: center !important;
    }
  }
}
.pcs-grid-container.ag-theme-dark .ag-row-focus {
  z-index: 1 !important;
}
