@import "src/styles/mixins";
@import "src/styles/themes";

.cancel-btn {
  background-color: "lightgrey";
}
.generate-map-btn {
  background-color: rgb(15, 125, 252) !important;
}

.map-flyout-indicator {
  justify-content: center;
}
.soil-info .svg-container{
  display:inline-block !important;
}
.soil-info svg{
  width:0.795rem !important;
  height:0.795rem !important;
  margin-left:0.1rem !important;
  padding-top:0.2rem !important;
}
.soil-tooltip {
    position: relative;
    display: inline-block;
    line-height:20px;
    z-index:10 !important;

  }

  .soil-tooltip .soil-tooltip-text {
    visibility: hidden;
    color:#333;
    min-width:200px;
   height:auto;
    border-radius: 6px;
    padding: 5px 5px 5px 5px;
    font-size: 12px;
    font-weight: 100;
    /* Position the tooltip */
    position: absolute;
        border: 1px solid #bfbfbf;
        background:#fff;
    box-shadow: 3px 3px 6px rgba(0, 0, 0, 0.1);
    top: 108%;
    margin-left: -185px;
    white-space:initial;
     z-index:9999 !important;
     word-wrap: break-word;
     text-align:start;

  }
  .soil-tooltip .soil-tooltip-text::after {
    content: "";
     position: absolute;
  z-index: 9999 !important;
  right: 5%;
  top: -10px;
  border-style: solid;
  border-width: 0 10px 10px 10px;
  border-color: transparent transparent #bfbfbf transparent;
  transition-duration: 0.3s;
  transition-property: transform;
  }
  .soil-tooltip:hover .soil-tooltip-text {
    visibility: visible;
   }

.map-generation-success {
  border: 1px transparent solid;
  @include rem-fallback(margin, 12px, 0px);
  @include rem-fallback(padding, 16px);
}
.map-generation-error {
  border: 1px transparent solid;
  @include rem-fallback(margin, 12px, 0px);
  @include rem-fallback(padding, 16px);
}

.date-picker-btn {
  background-color: transparent;
  margin-left: 5%;
  width: 240px;
  border-radius: 3px;
  border: 0.5px solid #afb9c3;
}
.bold {
  font-weight: bold;
  margin-top: 1%;
  margin-bottom: 2%;
}

.jobs-error {
  font-weight: bold;
  margin-top: 5%;
  margin-bottom: 2%;
  color: red;
}
.dateinput {
  width: 220px;
  text-align: center;
  border: 1px solid #afb9c3;
  margin-left: 20%;
}
.select-date-range {
  display: inline-flex;
}

.date-picker {
  margin-right: 5px;
}
.mapsflyout {
  overflow-y: scroll;
}
.radio {
  margin-bottom: 25px;
}
.flyouts-select-list {
  width: 230 px;
  height: 30 px;
  border: 1px solid #afb9c3;
}
.flyouts-text {
  width: 100% !important;;
}

.form-control-inputs {
  margin-top: 20px;
  line-height: 1.2;
  font-weight: bold;
  min-width: 500px;
  .form-control-group {
    width: 33%;
    display: inline-flex;
    padding: 0px 10px 0px 10px;

    .form-control-container {
      min-width: 60px;
      margin-right: 5px;
      width: 45%;
      .create-farms-input {
        margin-left: 5px;
        width: 100%;
        border: 1px solid #afb9c3;
        border-radius: 3px;
      }
    }
  }
}

.maps-flyout-container {
  @include rem-font-size(14px);

  .maps-flyout-header {
    @include rem-fallback(padding, 12px, 0px);
  }

  .maps-flyout-more-info {
    @include rem-fallback(padding, 12px, 0px);
  }

  .form-placeholder {
    font-style: italic;
    @include rem-fallback(margin, 60px, 30px);
  }

  .maps-flyout-error {
    border: 1px transparent solid;
    @include rem-fallback(margin, 12px, 0px);
    @include rem-fallback(padding, 16px);
  }

  .wait-indicator {
    @include rem-fallback(margin-left, 8px);
  }

  .summary-icon svg {
    @include square-px-rem(16px);
    @include rem-fallback(margin-left, 8px);
  }

  @include themify($themes) {
    color: themed("colorContentTextDim");

    .maps-flyout-header {
      color: themed("colorContentText");
    }
    .maps-flyout-more-info {
      color: themed("colorContentText");
    }
    .map-generation-error {
      border-color: themed("colorAlert");
    }

    .map-generation-success {
      border-color: themed("colorContentText");
    }
    .maps-flyout-error {
      border-color: themed("colorAlert");
    }

    .summary-icon svg {
      fill: themed("colorContentText");
    }
  }
}

#applyMapBtn {
  width: 40%;
  background-color: rgb(15, 125, 252);
  color: white;
  border-radius: 3px;
}

#applyMapBtnDisabled {
  width: 40%;
  background-color: rgb(202, 227, 255) !important;
  color: white;
  border-radius: 3px;
}
.soil-moisutre-measure{
 z-index:0;
}
