@import "src/styles/variables";
@import "src/styles/mixins";
@import "src/styles/themes";

.add-device-grid {
  height: 100%;
  .pcs-grid-container {
    height: 100%;
  }
  .pcs-grid-container.ag-theme-dark .ag-row.ag-row-selected {
    background-color: transparent;
  }

  .pcs-grid-container
    .ag-header-row
    .ag-header-cell
    .ag-cell-label-container
    .ag-header-cell-label
    .ag-header-cell-text {
    font-weight: bold;
    font-size: 13px;
  }
}
