$base-duration: 0.25s;

// Colors
$primary: yellow;
$green: #28B78C;
$red: #e74c3c;

.modal-footer {
  position: relative;
  //display: flex;
  text-align: center;
  align-items: center;
  justify-content: flex-end;
  width: 100%;
  margin: 0;
  padding: 1.25rem 0 0;

  .action {
    position: relative;
    //margin-left: 0.625rem;
    padding: 0.625rem 1.25rem;
    border: none;
    background-color: $primary;
    //border-radius: 0.25rem;
    color: white;
    font-size: 0.87rem;
    font-weight: 300;
    overflow: hidden;
    z-index: 1;

    &:before {
      position: absolute;
      content: '';
      top: 0;
      left: 0;
      width: 0%;
      height: 100%;
      background-color: rgba(255,255,255,0.2);
      transition: width $base-duration;
      z-index: 0;
    }

    &:last-child {
      background-color: $green;
    }

    &:first-child {
      background-color: white;//$red;
      margin-right: 1rem;
      color: $green;
    }

    &:hover {
      &:before {
        width: 100%;
      }
    }
  }
}
