.telemetry-panel {
  position: relative;
  box-shadow: 0px 0px 0px white;

  .telemetry-panel-container {
    display: flex;
    flex-flow: column nowrap;
    flex-grow: 1;
    overflow: auto;
    overflow-x:hidden;
  }

  .options-container {
    display: flex;
    flex-flow: row wrap;

    .telemetry-option {
      display: block;
      background: none;
      cursor: pointer;
      border: none;
      border-bottom: 2px solid transparent;
      @include rem-font-size(14px);
      @include rem-fallback(padding, 5px);
      @include rem-fallback(margin, 0px, 5px, 5px, 10px);
    }
  }

.bold-measure-type{
    button{
    color: #000000;
    font-weight: 600;
    }
  }

  .no-data {
    top: 75px
  }

  .measure-unit-container {
    text-align: center;
  }

  .time-series-explorer {
    display: block;
    @include rem-fallback(padding, 0px, 0px, 10px, 0px);
  }

  //For Buttons over telemetry Panel
  .date-picker-button,
  .live-button {
    background: #f9f9f9;
    border: 0.5px solid #bbbbbb;
    box-sizing: border-box;
    left: 75.21%;
    right: 17.08%;
    top: 28.22%;
    bottom: 68.56%;
    border-radius: 4px 0px 0px 4px;
    padding: 5px 15px 5px 15px;
    font-family: Segoe UI;
    line-height: normal;
    font-size: 0.8em;
  }

  .live-button {
    border-radius: 0px 4px 4px 0px;
  }

  .live-button:hover,
  .date-picker-button:hover,
  .live-button-active,
  .date-picker-button-active {
    color: #28b78c;
    border-color: #28b78c;
  }
  .panel-header .context-menu-align-container.right {
