@import "src/styles/themes";
@import "src/styles/mixins";

$inputPadding: 5px;
$containerMargin: 10px;
.context-menu-search-input {
  display: flex;
  flex-basis: 25%;
  @include rem-fallback(
    margin,
    $containerMargin,
    0px,
    $containerMargin,
    $containerMargin
  );

  .search-icon {
    @include rem-fallback(padding-left, $inputPadding);

    svg {
      @include square-px-rem(12px);
    }
  }

  input {
    background-color: inherit;
    border: none;
    color: inherit;
    font-family: inherit;
    text-overflow: ellipsis;
    width: 100%;
    @include rem-fallback(padding, $inputPadding);
  }

  .search-input::-webkit-input-placeholder {
    color: #c4c4c4;
  }

  @include themify($themes) {
    color: themed("colorContextSearchTextColor");
    background-color: themed("colorContextSearchBg");
    border: 1px solid themed("colorContextSearchBorderColor");

    .search-icon svg {
      fill: themed("colorContextSearchIconColor");
    }
  }
}
