@import 'src/styles/themes';
@import 'src/styles/mixins';

.pcs-renderer-cell.deviceModel {
  @include themify($themes) {
    &.node .pcs-renderer-icon svg { fill: themed('colorSystem'); }
    &.gateway .pcs-renderer-icon svg { fill: themed('colorWarning'); }
  }
}
