.properties-container {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    line-height: 22px;
    // margin-left: 10px; // This should be handled by section container

    table{
      flex-grow: 1;
      flex-basis: 45%; // 10% margin in between
      table-layout: fixed;
        td{
          vertical-align: top;
          padding: 3px;

          &.label{
            width: 120px;
          }

        }


        a{
            text-decoration: none;
            font-weight: bold;
            color: #0078D4;
        }
    }
}
