@import 'src/styles/mixins';
@import 'src/styles/themes';

.manage-error-section{
    position: relative;
    border: 1px solid  #333;
    margin: 1rem auto;
    padding: 0.7rem;
    font-size:16px;

    .flyout-section-content {
     padding-bottom:10px;
   }
   .success-message{
     font-size:16px;
   }
   .error-message{
    font-size:16px
   }
  }

.manage-users{
  display: flex;
  flex-direction: column;
  padding-left:20px;
  padding-right:20px;
  height:100%;
  .add-device-grid{
     height:100%;
     padding-bottom: 30px;
   }
  .delete-user{
    position:absolute;
    top:0;
    left:0;
    bottom:0;
    right:0;
  }
  .error-section{
    position: relative;
    border:1px solid  #333;
    margin: 1rem 0;
    padding: 0.7rem;
    bottom:5px;
    font-size:16px;
   width:100%;

    .flyout-section-content {
     padding-bottom:10px;
   }
   .success-message{
     font-size:16px;
   }
   .error-message{
    font-size:16px
   }
  }
  .form-control-inputs {
    line-height: 1.2;
    width:100%;
    margin-top:0px;
    min-width:auto;
    font-weight:normal;
    margin-bottom:20px;
  }
  .role-loader{
    margin-left:50%;
    margin-top:2%;
  }
.role-editor-input{
 margin-left: 5px;
    width: 95% !important;
    border: 1px solid #afb9c3 !important;
    border-radius: 3px;
    text-overflow: ellipsis;
    padding-right: 2px;
}

#applyRole{
    width: 10%;
    background-color: #0f7dfc;
    color: white;
    border-radius: 3px;
    position: absolute;
    left:5.5%;
    margin-top:2%;
  }

  #disableRole{
     width: 10%;
    background-color: #cae3ff;
    color: white;
    border-radius: 3px;
    position: absolute;
    left:5.5%;
    margin-top:2%;
  }

.form-select-role{margin-top:0px;}
.form-email{
width:55% !important;
display:flex;
padding-left:0px;
float:left;
}
.form-role{
  width:40% !important;
  display:flex !important;
  margin-top:0px !important;
}
.form-control-container {
        min-width: 80px;
        width: 100% !important;
        font-size: 14px;
        .error-message {
          font-weight: 100;
        }

        .farms-editor-input {
          margin-left: 5px;
          width: 95% !important;
          border: 1px solid #afb9c3 !important;
          border-radius: 3px;
          text-overflow: ellipsis;
          padding-right: 2px;

          .Select-control {
            font-weight: normal;
          }

          &.select-container {
            font-weight: normal;
          }

          &.error {
            margin-top: 0px;
            font-weight: 100;
          }
          .Select-menu-outer {
            margin-top: 1px !important;
            border-top: none !important;
            border: 1px solid #afb9c3 !important;
          }

          .Select-placeholder {
            width: 70%;
            display: flex;
            align-items: center;
            font-weight: 100;
            text-overflow: ellipsis !important;
          }

          .Select-control {
            border: none !important;

            .Select-input {
              height: 100%;
              input {
                height: 100%;
              }
            }
          }
        }
      }
}

.manage-filters-flyout-container{
  @include rem-font-size(14px);
  @include rem-fallback(padding-bottom, 16px);

  .borderless { border: none; }

  .edit-device-group-btn {
    background: none;
    border: none;
    cursor: pointer;
  }

  .device-grops-grid-container { @include rem-fallback(margin-top, 24px); }

  .add-btn {
    padding-left: 0;
    padding-right: 0;
    @include rem-fallback(margin, 24px, 0px, 24px, 0px);
  }

  .actions-button { @include rem-fallback(min-width, 110px); }

  .btn-toolbar {
    justify-content: flex-end;
    @include rem-fallback(padding-top, 32px);
  }

  @include themify($themes) {
    .property-grid-container .grid-header .cell { color: themed('colorContentTextDim'); }
    .edit-device-group-btn  { color: themed('colorContentText'); }
  }
}
@media screen and (max-width: 1440px){
 .manage-users{
   .add-device-grid{
     height:100%;
   }
 }
}

.delete-pending-indicator {
  padding-left: 32px;
}


.react-autosuggest__container {
  position: relative;
  width: 276px; // Agnivesh: This value has been carefully caliberated
}

.react-autosuggest__input {
  width: 286px; // Agnivesh: This value has been carefully caliberated
  height: 28px; // Agnivesh: If you touch this, you also have to change value at Label1
  padding: 0 10px;
  font-family: "Selawik", sans-serif;
  font-size: 14px;
  border: 1px solid #aaa;
  border-radius: 4px;
}

.react-autosuggest__input:focus {
  outline: none;
}

.react-autosuggest__container--open .react-autosuggest__input {
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
}

.react-autosuggest__suggestions-container {
  display: none;
}

.react-autosuggest__container--open .react-autosuggest__suggestions-container {
  display: block;
  position: absolute;
  top: 28px; // Label1
  width: 350px;
  border: 1px solid #aaa;
  background-color: #fff;
  font-family: "Selawik", sans-serif;
  font-size: 14px;
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
  z-index: 2;
  overflow: auto;
  max-height: 40rem;
}

.react-autosuggest__suggestions-list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.react-autosuggest__suggestion {
  cursor: pointer;
  padding: 10px 20px;
}

.react-autosuggest__suggestion:not(:first-child) {
  border-top: 1px solid #ddd;
}

.react-autosuggest__suggestion--focused {
  background-color: #0C7EAF;
  color: #fff;
}

.suggestion-content {
  display: flex;
  align-items: center;
  background-repeat: no-repeat;
}

.add-role-inputs {
  display: flex
}
