@import "src/styles/variables";
@import "src/styles/mixins";
@import "src/styles/themes";

.delete-info p{
  margin:0px;
  padding-bottom:5px;
}


.delete-modal-success {
  border: 1px transparent solid;
  @include rem-fallback(margin, 12px, 0px);
  @include rem-fallback(padding, 16px);
}
.delete-modal-error {
  border: 1px transparent solid;
  @include rem-fallback(margin, 12px, 0px);
  @include rem-fallback(padding, 16px);
}
.delete-modal-container {
  @include rem-fallback(font-size, 14px);

  .delete-header-container {
    display: grid;
    grid-template-columns: auto auto;
    justify-content: space-between;
    @include rem-fallback(padding-bottom, 33px);

  }
 .delete-info {
    @include rem-fallback(padding-bottom, 24px);
    @include rem-fallback(height, auto);
  }
 .delete-close-btn,
  .delete-close-btn:hover {
    background-color: transparent;
    @include rem-fallback(padding, 0px, 16px);
    @include rem-fallback(min-width, 14px);
    float:right;

    &:hover {
      background-color: transparent;
    }

    .btn-icon svg {
      @include square-px-rem(14px);
    }
  }

  @include themify($themes) {
    .delete-close-btn .btn-icon svg {
      stroke: themed("colorFlyoutText");
      background-color: transparent;
    }

    .delete-modal-error {
      border-color: themed("colorAlert");
    }

    .delete-modal-success {
      border-color: themed("colorContentText");
    }
  }
}
