.arrow_box {
	position: absolute;
	background: #0178d4;
  border: 4px solid #0178d4;
  height:130px;
  z-index:1;
  left:10px;
  top:90px;;
  color:#fff;
  width:286px;
  text-align:justify;
  box-shadow: 0px 1.2px 3.6px rgba(0, 0, 0, 0.108), 0px 6.4px 14.4px rgba(0, 0, 0, 0.132);
  border-radius: 2px;
  font-size:14px;
}

.arrow_box .title{
  margin:15px;
  margin-bottom:0px;
  margin-left:0px;
}
 .arrow_box p {
  margin-bottom: 0px;
  text-align: justify;
  line-height: 1.2rem;
  padding: 5px;
  margin-top:0px;
}

.arrow_box:after, .arrow_box:before {
	bottom: 100%;
	left: 50%;
	border: solid transparent;
	content: " ";
	height: 0;
	width: 0;
	position: absolute;
	pointer-events: none;
}

.arrow_box:after {
	border-color: rgba(136, 183, 213, 0);
	border-bottom-color: #0178d4;
	border-width: 10px;
	margin-left: -45%;
}
.arrow_box:before {
	border-color: rgba(194, 225, 245, 0);
	border-bottom-color: #0178d4;
	border-width: 12px;
	margin-left: -46%;
}

.arrow_box svg{
  fill:#fff;
  width:1.5rem;
  height:1.5rem;
  float:right;
}
.arrow_box .svg-container{
  justify-content: flex-end;
  position:absolute;
  right:0px;
}

.arrow_box .svg-container :hover{
  cursor:pointer;
}
