@import "src/styles/mixins";
@import "src/styles/themes";

.geo-json {
  display: flex;
  flex-flow: column nowrap;
  padding: 0 !important;
  background-color: transparent !important;

  @include themify($themes) {
    color: themed("colorDashboardPanelText");
  }

}

.no-box-shadow {
  box-shadow: 0px 0px 0px white;
}

.warning-text {
  color: red;
  font-size: 14px;
  padding-left: 4px;
}

#geo-string {
  border: 1px solid #afb9c3;
  border-radius: 3px;
  height: 27rem;
  width: 100%;
  margin-top:2%;
}

#invalid-input {
  border: 1px solid red;
  border-radius: 3px;
  height: 27rem;
  width: 100%;
}

#geo-string::-webkit-input-placeholder {
  color: #c4c4c4;
}
