.app-main {
  display: flex;
  flex-grow: 1;
  flex-flow: column nowrap;
  height: 100%;
  position: relative;

  .main-container {
    display: flex;
    flex-flow: column nowrap;
    height: 100%;
    width: 100%;
    // Add positioning to fix bug in AgGrid when used inside flexbox
    // Issue: When the page is resized, the width expands but never contracts
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;

    // This is to keep the extra div that app insights needs for withAITracking
    // from messing up all the sphagetti CSS. TODO: The day appinsights allows
    // you to set a class to the div they create, fix this
    &>[class=""] {
      display: flex;
      flex-direction: column;
      flex-grow: 1;
      overflow: auto;
    }

  }
}
